.newsletter{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction: column;
   align-items: center;
   padding: 0px 140px;
   margin-bottom: 150px;
   background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 70%);
   justify-content: center;
   gap: 30px;
   margin: auto;
   padding-top: 50px;
   padding-bottom: 150px;

}

.newsletter h1{
    color: #454545;
    font-size: 50px;
    font-weight: 600;
}

.newsletter p{
    color: #454545;
    font-size: 20px;
}
.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
}

.newsletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}

.newsletter div button{
    width: 200px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}